import React from "react";
import * as Semantic from "semantic-ui-react";

const LayoutApplication = ({ header, children }) => {
  const [activeItem, setActiveItem] = React.useState({
    activeItem: "reviews",
  });

  const [isLogging, setLogging] = React.useState(false);

  const handleItemClick = (e, { name }) => setActiveItem(name);

  return (
    <Semantic.Container style={{ minHeight: "100vh" }}>
      <Semantic.Header as="h2" icon textAlign="center">
        {header}
      </Semantic.Header>

      <Semantic.GridRow>
        <Semantic.Menu pointing secondary>
          <Semantic.Menu.Item
            name="editorials"
            active={activeItem === "editorials"}
            onClick={handleItemClick}
          >
            Page 1
          </Semantic.Menu.Item>

          <Semantic.Menu.Item
            name="reviews"
            active={activeItem === "reviews"}
            onClick={handleItemClick}
          >
            Page 2
          </Semantic.Menu.Item>

          <Semantic.Menu.Item
            name="upcomingEvents"
            active={activeItem === "upcomingEvents"}
            onClick={handleItemClick}
          >
            Page 3
          </Semantic.Menu.Item>

          <Semantic.Menu.Menu position="right">
            <Semantic.Menu.Item
              name="login"
              active={activeItem === "login"}
              onClick={handleItemClick}
            >
              <i className="icons">
                {() => {
                  if (isLogging) {
                    return (
                      <i
                        aria-hidden="true"
                        className="circle notch big loading icon"
                      ></i>
                    );
                  }

                  return;
                }}

                <i aria-hidden="true" className="user icon"></i>
              </i>
            </Semantic.Menu.Item>
          </Semantic.Menu.Menu>
        </Semantic.Menu>
      </Semantic.GridRow>

      {children}
    </Semantic.Container>
  );
};

export default LayoutApplication;
