import React from "react";
import { Route, Switch } from "react-router-dom";
import Portfolio from "./../container/view/portfolio";
import Skill from "./../container/view/skill";
import About from "./../container/view/about";
import PageFinanceApp from "./../container/page/finance-app/PageFinanceApp";

const MainRoute = () => {
  return (
    <Route>
      <div>
        <Switch>
          <Route exact path="/" component={About} />
          <Route path="/skill" component={Skill} />
          <Route path="/portfolio" component={Portfolio} />
          <Route
            exact
            path="/files/resume"
            render={() => (window.location = "https://localhost/files/resume")}
          />
          <Route path="/application/finance" component={PageFinanceApp} />
        </Switch>
      </div>
    </Route>
  );
};

export default MainRoute;
